body {
  margin: 0;
  padding: 0;
}

html {
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.container {
  visibility: hidden;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.imageContainer {
  width: 800px;
  height: 500px;
  position: relative;
  overflow: hidden;
  background: red;
}

.imageContainer img {
  width: 100%;
  height: 100%;
  position: absolute;
}
.imageContainer::after {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  right: 0;
  background-color: #ffccd5;
}

.inner {
  position: absolute;
  font-size: 5rem;
  font-weight: bold;
  z-index: 100;
  margin: 0;
  text-transform: uppercase;
  letter-spacing: 0.5em;
}

.scroll {
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  background: lightgray;
  overflow: hidden;
  margin: 0;
}

.line {
  position: relative;
  height: 24vh;
}

.line h1 {
  position: absolute;
  overflow: hidden;
  white-space: nowrap;
  font-size: 10vh;
  color: grey;
  text-transform: uppercase;
  margin: 0;
}

.text {
  right: -100vw;
  top: 0;
  background: #ff4d6d;
}

.textRev {
  left: -100vw;
  bottom: 0;
  background: #ff758f;
}

.footer {
  height: 100vh;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.footer h1 {
  font-size: 5rem;
}

.containerLeft {
  position: absolute;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  max-height: 100vh;
  width: 100vw;
  background-color: #ffafcc;
  z-index: 10;
}

.left h1 {
  font-size: 3rem;
}

.left,
.right {
  width: 50%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.right {
  position: relative;
  height: 100vh;
  background-color: red;
}

.right img {
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.containerRight {
  position: absolute;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  max-height: 100vh;
  width: 100vw;
  background-color: #ffc8dd;

  z-index: 11;
}

.left1,
.right1 {
  width: 50%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.right1 {
  position: relative;
  height: 100vh;
  background-color: red;
}

.right1 img {
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.left1 h2 {
  font-size: 3rem;
  text-align: center;
}

.containerBottom {
  position: absolute;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  max-height: 100vh;
  width: 100vw;
  background-color: #ffafcc;
  z-index: 12;
}

.left2,
.right2 {
  width: 50%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.right2 {
  position: relative;
  height: 100vh;
  background-color: red;
}

.right2 img {
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.left2 h2 {
  font-size: 3rem;
  text-align: center;
}

.final {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  flex-direction: column;
}

.final h1 {
  font-size: 4rem;
  text-transform: uppercase;
}

.final p {
  font-size: 1.2rem;
  color: gray;
}
